.projects {
	background-image: url('./images/45-degree-fabric-light2.png'),
		linear-gradient(120deg, #385b19, #93bb65);
	width: 100%;
	height: fit-content;
	padding-bottom: 5vw;
}

.projectsTitle {
	font-size: 100px;
	font-family: 'subtitle';
	padding-top: 20px;
	display: inline;
}

.projectCard {
	width: 100%;
	/* min-width: 500px; */
	height: auto;
	margin: 30px auto 0 auto;
	position: relative;
	-webkit-box-shadow: 5px 5px 5px 1px #000000;
	box-shadow: 5px 5px 5px 1px #000000;
}

.projectCard img {
	width: 100%;
	height: 100%;
}

.projectCard img:hover + .projectInfo,
.projectInfo:hover {
	opacity: 1;
}

.projectInfo {
	background-color: rgba(0, 0, 0, 0.788);
	position: absolute;
	top: 0;
	width: 100%;
	height: 100%;
	display: grid;
	transition: opacity 300ms;
	opacity: 0;
	grid-template-columns: 50% 50%;
	grid-template-rows: 15% 20% 39%;
	color: #c9b5a5;
}

.projectInfo .nameContainer {
	grid-column: 1/-1;
	grid-row: 1/2;
}

.projectInfo .demoContainer {
	grid-column: 1/2;
	grid-row: 3/4;
	justify-self: end;
	align-self: center;
}

.projectInfo .codeContainer {
	grid-column: 2/-1;
	grid-row: 3/4;
	justify-self: start;
	align-self: center;
}

.projectInfo .tagContainer {
	grid-column: 1/-1;
	grid-row: 2/3;
}

.projectInfo .techContainer {
	grid-column: 1/-1;
	grid-row: 4/-1;
}

.projectInfoName {
	margin: auto;
	font-size: 50px;
}

.projectInfoTag {
	margin: 25px auto;
	font-size: 13px;
}

.projectInfo .tech {
	margin: 0px auto;
	font-size: 20px;
}

.button {
	background: none;
	border: 2px solid;
	border-radius: 20%;
	font-size: 20px;
	line-height: 1;
	margin: 0.5em;
	padding: 1em 2em;
	font-weight: 600;
}

.demo {
	background-image: linear-gradient(180deg, rgb(31, 150, 31), rgb(22, 97, 22));
}

.code {
	background-image: linear-gradient(180deg, rgb(190, 48, 48), rgb(160, 13, 13));
}

.demo:hover {
	cursor: pointer;
	animation: demo 1s;
	box-shadow: 0 0 0 10px rgba(255, 255, 255, 0);
}

.code:hover {
	cursor: pointer;
	animation: code 1s;
	box-shadow: 0 0 0 13px rgba(255, 255, 255, 0);
}

@keyframes demo {
	0% {
		box-shadow: 0 0 0 0 rgb(56, 207, 56);
	}
}

@keyframes code {
	0% {
		box-shadow: 0 0 0 0 rgb(209, 29, 29);
	}
}

.durakCard,
.igfCard {
	opacity: 0;
}

/* Arrows */
#arrow1 {
	width: 60px;
	height: 72px;
	margin-left: -80px;
	margin-top: 20px;

	display: inline;
}

#arrow2 {
	width: 60px;
	height: 72px;
	margin-left: 15px;
	margin-top: 20px;

	display: inline;
}

.arrows path {
	stroke: black;
	fill: transparent;
	stroke-width: 1px;
	animation: arrow 5s infinite;
	-webkit-animation: arrow 5s infinite;
	animation-delay: 10s;
	-webkit-animation-delay: 10s;
}

@keyframes arrow {
	0% {
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
	80% {
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}

@-webkit-keyframes arrow /*Safari and Chrome*/ {
	0% {
		opacity: 0;
	}
	40% {
		opacity: 1;
	}
	80% {
		opacity: 0;
	}
	100% {
		opacity: 0;
	}
}

.arrows path.a1 {
	animation-delay: -1s;
	-webkit-animation-delay: -1s; /* Safari 和 Chrome */
}

.arrows path.a2 {
	animation-delay: -0.5s;
	-webkit-animation-delay: -0.5s; /* Safari 和 Chrome */
}

.arrows path.a3 {
	animation-delay: 0s;
	-webkit-animation-delay: 0s; /* Safari 和 Chrome */
}
